"use client";

import { useState } from 'react';

interface Post {
  username: string;
  content: string;
  timestamp: string;
}

export default function Home() {
  const [isLoading, setIsLoading] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  // Mock post data
  const mockPost: Post = {
    username: "@alice.bsky.social",
    content: "Just discovered an amazing new constellation! üåü The universe never ceases to amaze me. #Astronomy #SpaceExploration",
    timestamp: "2024-01-18T15:30:00Z"
  };

  const handleLogin = async () => {
    setIsLoading(true);
    // The actual implementation will use the Bluesky OAuth flow
    const identifier = "bsky.app";
    const redirectUri = `${window.location.origin}/callback`;
    
    try {
      // This is a placeholder URL - you'll need to replace it with your actual backend endpoint
      const authUrl = `https://bsky.app/login?identifier=${identifier}&redirect=${encodeURIComponent(redirectUri)}`;
      window.location.href = authUrl;
    } catch (error) {
      console.error('Login failed:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleReaction = async (reaction: 'yay' | 'nay') => {
    try {
      // TODO: Implement reaction logic with your backend
      console.log(`Reacted with ${reaction}`);
    } catch (error) {
      console.error(`Failed to react with ${reaction}:`, error);
    }
  };

  return (
    <>
      {/* Dev Toolbar */}
      <div className="w-full bg-gray-800 text-white p-2 flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <span className="font-mono text-sm">DEV MODE</span>
          <div className="flex items-center space-x-2">
            <span className="text-sm">Auth:</span>
            <button
              onClick={() => setIsAuthenticated(!isAuthenticated)}
              className={`px-3 py-1 rounded text-sm font-medium ${
                isAuthenticated ? 'bg-green-500' : 'bg-red-500'
              }`}
            >
              {isAuthenticated ? 'ON' : 'OFF'}
            </button>
          </div>
        </div>
      </div>

      <main className="min-h-screen flex flex-col items-center p-8">
        {/* Title */}
        <h1 className="text-4xl font-bold mb-12">Yaynaysky</h1>

        {/* Latest Post Card */}
        <div className="w-full max-w-2xl bg-white rounded-lg shadow-lg p-6 mb-8">
          <div className="flex items-start space-x-4">
            <div className="flex-1">
              <p className="font-semibold text-lg">{mockPost.username}</p>
              <p className="text-gray-600 mt-2">{mockPost.content}</p>
              <p className="text-sm text-gray-400 mt-2">
                {new Date(mockPost.timestamp).toLocaleString()}
              </p>
            </div>
          </div>
        </div>

        {/* Authentication/Reaction Buttons */}
        <div className="flex gap-4">
          {isAuthenticated ? (
            <>
              <button
                onClick={() => handleReaction('nay')}
                className="bg-red-500 hover:bg-red-600 text-white font-bold py-3 px-6 rounded-lg transition-colors duration-200"
              >
                üëé Nay
              </button>
              <button
                onClick={() => handleReaction('yay')}
                className="bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-6 rounded-lg transition-colors duration-200"
              >
                üëç Yay
              </button>
            </>
          ) : (
            <button
              onClick={handleLogin}
              disabled={isLoading}
              className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-3 px-6 rounded-lg transition-colors duration-200 disabled:opacity-50"
            >
              {isLoading ? 'Connecting...' : 'Login with Bluesky'}
            </button>
          )}
        </div>
      </main>
    </>
  );
}
